import Vue from 'vue'
import App from './App.vue'
import VueMaterial from 'vue-material'
import router from './route';

import '../node_modules/vue-material/dist/vue-material.css';

Vue.use(VueMaterial)
Vue.router = router
Vue.config.productionTip = false

new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app')



